<template>
	<div class="form-check">
		<input class="form-check-input" type="checkbox" :id="field_id" :name="field_id" v-model="checked" true-value="1" false-value="0" :disabled="disabled" @change="changed()">
		<label class="form-check-label" :for="field_id">
			{{ checkbox_label }}
		</label>
	</div>
</template>

<script>
	import State from '@/store/State.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				checked: '0',
			}
		},
		methods: {
			changed() {
				State.save_user_input(this.field_id, this.checked);
			},
		},
		computed: {
			disabled() {
				if (this.field_data.disabled === 'true') {
					return true;
				}
				return false;
			},
			checkbox_label() {
				return this.field_data.label;
			},
		},
		mounted() {
			var user = State.retrieve_user_input(this.field_id);
			if (user !== undefined) {
				this.checked = user;
			} else if (this.field_data.value !== null) {
				this.checked = this.field_data.value;
				State.save_user_input(this.field_id, this.field_data.value);
			} else {
				this.checked = 0;
			}
		},
	}
</script>
