<template>
	<input type="text" class="form-control" :id="field_id" :name="field_id" :placeholder="placeholder" v-model="value" :disabled="disabled" @change="changed()">
</template>

<script>
	import State from '@/store/State.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				value: '',
			}
		},
		methods: {
			changed() {
				State.save_user_input(this.field_id, this.value);
			},
		},
		computed: {
			placeholder() {
				return this.field_data.placeholder;
			},
			html_validation() {
				// TODO
				// html validation via attributes, f.e. maxlength="10"
				if (this.field_data.attributes === undefined) {
					return '';
				} else {
					var attributes = '';
					for (var key in this.field_data.attributes) {
						attributes += key + '="' + this.field_data.attributes[key] + '" ';
					}
					return attributes;
				}
			},
			disabled() {
				if (this.field_data.type_settings.disabled === 'true') {
					return true;
				} else {
					return false;
				}
			},
		},
		mounted() {
			var user = State.retrieve_user_input(this.field_id);
			if (user !== undefined) {
				this.value = user;
			} else if (this.field_data.value !== null) {
				this.value = this.field_data.value;
				State.save_user_input(this.field_id, this.field_data.value);
			} else {
				this.value = '';
			}
		},
	}
</script>
