import State from '@/store/State.js';

/**
 * Functions
*/
class Functions
{
	/**
	 * serialize_user_input
	*/
	serialize_user_input()
	{
		var inputs = State.get_all_user_inputs();
		var serialized = [];

		for ( var key in inputs ) {
			if ( ! inputs.hasOwnProperty( key ) ) { continue; }

			var value = encodeURIComponent( key ) + '=' + encodeURIComponent( inputs[key] );
			serialized.push( value );

		}

		return serialized.join( '&' );
	}

	/**
	 * global_parameter
	*/
	global_parameter()
	{
		if ( State.endpoint_type() !== 'stage' ) {
			return '&global=0'; // old way, sending step1 and globalForm in one request
		}
		if ( State.current_step() === 1 ) {
			return '&global=2'; // ask for | validate only step1 form
		} else if ( State.current_step() === 2 ) {
			return '&global=1'; // ask for | validate only globalForm separately
		} else {
			return '';
		}
	}

	/**
	 * field_format_parameter
	*/
	field_format_parameter()
	{
		return 'json=1';
	}

	/**
	 * add_token_as_parameter (used to be in Session)
	*/
	add_token_as_parameter()
	{
		var token = State.get_session_token();
		if ( token === '' ) {
			return '';
		}
		return '&token=' + token;
	}
}

export default new Functions;
